import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { Department, DepartmentService } from 'modules/organization/department/department.service'
import { Authorization } from 'core/services/authorization.service';

@Component({
  selector: 'app-departments',
  templateUrl: './list-department.component.html',
  providers: [ DepartmentService, Authorization ],
  encapsulation: ViewEncapsulation.None
})
export class ListDepartmentComponent implements OnInit {

  departments: Department[];

  createAllowed: boolean = false;
  editAllowed: boolean = false;

  constructor(private departmentService: DepartmentService, private authorization: Authorization) {
    this.departments = [];
  }

  async ngOnInit() {

    this.departmentService.getDepartments().then(depts => this.departments = depts);

    this.createAllowed = await this.authorization.hasAccess({
      entity: 'organization:department',
      action: 'create:post'
    });

    this.editAllowed = await this.authorization.hasAccess({
      entity: 'organization:department',
      action: 'update:put'
    });
  }

  noOfDesignations() {
    let total = 0;
    this.departments.forEach (x => total += x.designations.length);
    return total;
  }

}
