import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ListDepartmentComponent } from './list-department/list-department.component';
import { ManageDepartmentComponent } from './manage-department/manage-department.component';
import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Department'
    },
    children: [
      {
        path: '',
        redirectTo: 'list'
      },
      {
        path: 'list',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'List',
          access: {
            entity: 'organization:department',
            action: 'find:get'
          }
        },
        component: ListDepartmentComponent
      },
      {
        path: 'add',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Add',
          access: {
            entity: 'organization:department',
            action: 'create:post'
          }
        },
        component: ManageDepartmentComponent
      },
      {
        path: 'edit/:id',
        data: {
          title: 'Edit',
          access: {
            entity: 'organization:department',
            action: 'update:put'
          }
        },
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        component: ManageDepartmentComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DepartmentRoutingModule { }
