import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Department, DepartmentService } from 'modules/organization/department/department.service'

import { SuccessModalComponent  } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';

@Component({
  selector: 'app-manage-department',
  templateUrl: './manage-department.component.html',
  providers: [ DepartmentService ],
  encapsulation: ViewEncapsulation.None
})
export class ManageDepartmentComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  data: Department = new Department();

  error: string = '';

  constructor(private route: ActivatedRoute, private departmentService: DepartmentService) {

    if (this.route.snapshot.params['id']) {
      this.data._id = this.route.snapshot.params['id'];
    }
  }

  ngOnInit() {

    if (this.data._id) {

      this.departmentService.getDepartment(this.data._id)
        .then(department => {
          this.data = department;
        });

    } else {
      this.addNewDesignation();
    }
  }

  addNewDesignation() {

    this.data.designations.push('');
    this.data.designations = this.data.designations.slice();
  }

  removeDesignation(i) {

    this.data.designations.splice(i, 1);
  }

  postDepartment() {

    this.error = '';
    if (this.data._id && this.data._id.toString()) {

      this.departmentService.putDepartment(this.data).then(response => {

        this.successModal.show('/organization/department');

      }).catch(err => {

        this.error = err.error.message.text;
        this.errorModal.show();
      });

    } else {

      this.departmentService.postDepartment(this.data).then(response => {

        this.successModal.show('/organization/department');

      }).catch(err => {

        this.error = err.error.message.text;
        this.errorModal.show();
      });
    }
  }

  trackByIndex(index: number, value: number) {
    return index;
  }
}
